import React, { Component } from "react";
import { Grid, Cell } from "react-mdl";
import GithubLogo from "../Pics/github.png";
import LinkedinLogo from "../Pics/linkedin.png";
import MyPic from "../Pics/me_grad_better_edited_tree.png";
import AOS from "aos";
import "aos/dist/aos.css";

class LandingPage extends Component {
  render() {
    return (
      <div style={{ width: "100%", margin: "auto", marginTop: "50px" }}>
        <Grid className="landing-grid">
          <Cell col={12}>
            <div className="banner-text" data-aos="fade-up">
              <img
                src={MyPic}
                alt="avatar"
                className="avatar-img"
                data-aos="fade-down"
              />
              <div className="social-links" data-aos="slide-left">
                {/* LinkedIn */}
                <a
                  href="https://www.linkedin.com/in/anthonyhsia22/"
                  target="_blank"
                >
                  <img src={LinkedinLogo} />
                </a>

                {/* Github */}
                <a href="https://github.com/ahsia3" target="_blank">
                  <img src={GithubLogo} />
                </a>
              </div>
              <hr />
              <h1 data-aos="slide-right">Anthony Hsia</h1>
              <hr />
              <p data-aos="slide-left">
                <span className="proficient">HTML/CSS | Java </span>
                <span className="efficient">
                  | Python | SQL | JavaScript | C# | C | x86 Assembly
                </span>
              </p>
            </div>
          </Cell>
        </Grid>
      </div>
    );
  }
}

export default LandingPage;
